<template>
    <div class="body">
      <div class="bodyNav">
        发布>每日主持词
      </div>
      <div class="informationBox">
        <h2>基本信息</h2>
        <div class="changeTeacher">
          <p> 选择老师 <span>*</span></p>
          <drop-down
            @getChangeTea = getTea
            :itemlist="itemlist"
          ></drop-down>
        </div>
        <div class="title">
          <p>标题 <span>*</span></p>
          <input type="text" placeholder="请输入标题（建议在15个字符以内）" v-model="titleTxt">
          <h5>0/15</h5>
        </div>
        <div class="audio">
          <p>音频上传 <span>*</span></p>
          <token
          ></token>
          <h5>格式支持mp3、m4a，为保证音频加载与播放的流畅性，建议上传大小不超过500M</h5> <br/>
        </div>
        <div class="image">
          <p>上传头像 <span>*</span></p>
          <token2
            class="token2"
            :beForeImg = beForeImg
          ></token2>
          <h5>建议尺寸300*300px或1:1，JPG,PNG格式，图片小于5M</h5>
        </div>
        <div class="name">
          <p>老师名字 <span>*</span></p>
          <input type="text" placeholder="请输入老师名字（建议在5个字符以内）" v-model="teacherName">
          <h5>0/5</h5>
        </div>
        <div class="details">
          <p>图文详情 <span>*</span></p>
          <div>
            <div class="editor-container">
              <u-e :defaultMsg=defaultMsg :config=config ref="ue"></u-e>
            </div>
          </div>
        </div>
        <h2 class="setUp">上架设置</h2>
        <div class="time">
          <p>上架时间 <span>*</span></p>
          <el-radio-group v-model="radio2" class="radioBox" @change="changeValue">
            <el-radio :label="0">立即上架</el-radio>
            <el-radio :label="1">定时上架</el-radio>
            <el-date-picker
              v-model="setUpTime"
              type="datetime"
              placeholder="选择日期时间"
              v-if="showDate"
            >
            </el-date-picker>
            <el-radio :label="2">暂不上架</el-radio>
          </el-radio-group>
        </div>
        <div class="submit" @click="goSumbit">
          点击提交
        </div>
      </div>
    </div>
</template>
<script>
  import token from '../components/token'
  import token2 from '../components/token2'
  import UE from '../components/UE'
  import axios from "axios"
  import dropDown from "../components/dropDown"
  import Bus from './bus.js'
  import $ from 'jquery'
  let tokens
  let getToken
    export default {
      name: "releaseEveryCenter",
      components:{
        token,
        token2,
        UE,
        dropDown
      },
      data(){
          return{
            itemlist:[],
            publicTeaMrg:[],
            defaultMsg: '',
            config: {
              initialFrameWidth: 1000,
              initialFrameHeight: 400
            },
            titleTxt:"",
            teacherName:"",
            headers:{
              authorization: 'authorization-text',
            },
            radio2: 3,
            pickerOptions: {
              shortcuts: [{
                text: '今天',
                onClick(picker) {
                  picker.$emit('pick', new Date());
                }
              },
              {
                text: '昨天',
                onClick(picker) {
                  const date = new Date();
                  date.setTime(date.getTime() - 3600 * 1000 * 24);
                  picker.$emit('pick', date);
                }
              },
              {
                text: '一周前',
                  onClick(picker) {
                    const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', date);
                  }
               }
              ]
            },
            showDate:false,
            msecUp:"",
            value1: '',
            beForeImg:"",
            lastHeadImg:"",
            lastAudioUrl:"",
            lastUpTime:"",
            teacherId:"",
            lastContent:"",
            setUpTime: '',
          }
        },
      mounted(){
        //通过bus接受token组件的音频
        Bus.$on('passAudio', (e) => {
          this.lastAudioUrl = e
          console.log(this.lastAudioUrl)
        });
      },
      methods:{
  //提交按钮获取所有文本
        goSumbit(){
          console.log(tokens)
          this.lastHeadImg = $(".token2").children(".picss").children("img").attr("src")
          //获取富文本编辑器内容
          let content = this.$refs.ue.getUEContent();
          this.$notify({
            title: '',
            message: content,
            type: 'success'
          });
          this.lastContent = content
          //立即上架
          if(this.lastUpTime === 0){
            let _that = this;
            //用户登录请求token
              let lastMegs = this.$qs.stringify({
                authorId : _that.teacherId,
                nickName : _that.teacherName,
                avatar : _that.lastHeadImg,
                title : _that.titleTxt,
                content : _that.lastContent,
                audioURL : _that.lastAudioUrl,
                publishTime: new Date(),
                courseType:"1"
              })
              axios.post("https://xcx.guanghe520.com/courseware/create",lastMegs,{
                headers:{
                  Authorization:tokens
                }
              }).then((res)=>{
                alert(res.data.message);
                this.$router.push({path:"/"})
              }).catch((err)=>{
                alert(err)
              })
          }
          if(this.lastUpTime === 1){
              let _that = this;
              //用户登录请求token
                  let lastMegs = this.$qs.stringify({
                    authorId : _that.teacherId,
                    nickName : _that.teacherName,
                    avatar : _that.lastHeadImg,
                    content : _that.lastContent,
                    title : _that.titleTxt,
                    audioURL : _that.lastAudioUrl,
                    publishTime:_that.setUpTime,
                    courseType:"1"
                  });
                  axios.post("https://xcx.guanghe520.com/courseware/create",lastMegs,{
                    headers:{
                      Authorization:tokens
                    },
                  }).then((res)=>{
                    console.log(res)
                  });
            alert("定时上架成功")
          }
          if(this.lastUpTime === 2){
            alert("暂不上架成功")
          }
        },
        changeValue(value){
          //根据单选框的点击判断，假如选中了定时上传，才显示时间选择器
          this.showDate = this.radio2 === 1;
          //判断点击的是哪个，把他传给最终上传内容的按钮
          if(value === 0){
            this.lastUpTime = 0
          }
          if(value === 1){
            this.lastUpTime = 1
          }
          if(value === 2){
            this.lastUpTime = 2
          }
        },
        //通过子组件事件把选中的老师所有信息拿过来，进行渲染；
        getTea(meg){
          this.publicTeaMrg = meg
          this.teacherName = this.publicTeaMrg.nickName
          this.beForeImg = this.publicTeaMrg.avatar
          this.teacherId = this.publicTeaMrg._id
        },
      },
      created(){
        //登陆用户获取token
        getToken = this.$qs.stringify({
          name:"gh_sunwh",
          password:"111111"
        });
        let _that = this;
        axios.post("https://xcx.guanghe520.com/user/signin",getToken)
          .then((res)=>{
            tokens = res.data.token
            //获取所有老师的信息
            axios.get("https://xcx.guanghe520.com/user/teacherList",{
              headers:{
                Authorization:tokens
              }
            })
              .then((res)=>{
                _that.itemlist = res.data.teachers
                console.log(res)
              })
              .catch((err)=>{
                console.log(err)
              })
          })
          .catch((err)=>{
            console.log(err)
          })
      }
    }
</script>
<style scoped>
  .body .informationBox .changeTeacher{
    width: 100%;
    height: 80px;
    margin-top: 20px;
    display: flex;
    align-items: start;
    margin-left: 20px;
  }
  .body .informationBox .changeTeacher p{
    font-size: 14px;
    font-weight: 600;
    color: #000;
  }
  .body .informationBox .changeTeacher span{
    color: #f00;
  }

