<template>
  <div class="body">
    <div class="bodyContent">
      <div class="bodyContentHead">案例研习室</div>
      <div class="bodyContentNav">
        <div class="release" @click="releaseClick">发布内容</div>
      </div>
      <div class="bodyName">
        <p>历史发布</p>
        <p>状态</p>
        <p>上架时间</p>
        <p>操作</p>
      </div>
      <div class="bodyCenterBox">
         <case-host-center></case-host-center>
      </div>
    </div>
  </div>
</template>
<script>
import caseHostCenter from "./caseHostCenter"
  export default {
    name: "caseHost",
    data(){
      return{
      }
    },
    components:{
      caseHostCenter
    },
    methods:{
      releaseClick(){
        this.$router.push({ path:'/caseStudyRoom'})
      }
    }
  }
</script>

<style scoped>
  .body{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .body .bodyContent{
    width: 100%;
    height: 100%;
  }
  .body .bodyContent .bodyContentHead{
    width: 100%;
    height: 70px;
    background-color: #fff;
    font-size: 20px;
    line-height: 70px;
    color: #000;
    padding-left: 30px;
    font-weight: 600;
  }
  .body .bodyContent .bodyContentNav{
    width: 98%;
    height: 70px;
    background-color: white;
    margin: 10px auto;
    display: flex;
    align-items: center;
  }
  .body .bodyContent .bodyContentNav .release{
    width: 100px;
    height: 38px;
    background-color: #FF890E;
    font-size: 14px;
    color: #fff;
    line-height: 38px;
    text-align: center;
    margin-left: 17px;
  }
  .body .bodyContent .bodyContentNav .release:hover{
    cursor: pointer;
  }
  .body .bodyContent .bodyName{
    width: 98%;
    height: 20px;
    padding: 0 12px;
    display: flex;
  }
  .body .bodyContent .bodyName p{
    font-size: 12px;
    font-weight:600;
    line-height: 20px;
    color: #000;
  }
  .body .bodyContent .bodyName p:nth-child(1){
    width: 35%;
    text-align: left;
    padding-left: 10px;
  }
  .body .bodyContent .bodyName p:nth-child(2){
    width: 20%;
    text-align: center;
  }
  .body .bodyContent .bodyName p:nth-child(3){
    width: 20%;
    text-align: center;
  }
  .body .bodyContent .bodyName p:nth-child(4){
    width: 25%;
    text-align: center;
  }
  .body .bodyContent .bodyCenterBox{
    width: 98%;
    height: 520px;
    background-color: #fff;
    margin: 10px auto;
    padding: 12px;
    overflow-y: scroll;
  }
</style>



