<template>
    <div class="body">
      <div class="bodyhead">
        注册老师
      </div>
      <div class="bodyCenter">
        <h2>基本信息</h2>
        <div class="megBox">
          <div class="nickName">
            <p>老师昵称<span>      *</span></p>
            <input type="text" placeholder="老师昵称" v-model="nickNameV"></input>
          </div>
          <div class="nickName">
            <p>用户名<span>      *</span></p>
            <input type="text" placeholder="用户名" v-model="nameV"></input>
          </div>
          <div class="nickName">
            <p>密码<span>      *</span></p>
            <input type="text" placeholder="密码" v-model="passwordV"></input>
          </div>
          <div class="headImg">
            <p>头像</p>
            <token2></token2>
          </div>
          <div class="regBtn" id="regBtnId" @click="clickReg">点击注册</div>
        </div>
      </div>
    </div>
</template>

<script>
  import axios from 'axios'
  import token2 from "./token2"
  import Bus from './bus.js'
    export default {
      name: "regTeacherRight",
      components:{
          token2
      },
      data(){
        return{
          nickNameV:"",
          nameV:"",
          passwordV:"",
          headImgV:""
        }
      },
      //通过bus拿到token2组件img路径
      created(){
        Bus.$on('msg', (e) => {
          this.headImgV = e;
          console.log(e)
        });
      },
      methods:{
        //点击提交注册老师
        clickReg(){
          //声明token，用户登录请求token
          let getToken = this.$qs.stringify({
            name:"gh_sunwh",
            password:"111111"
          });
          let _that = this;
          //用户登录请求token
          axios.post("https://xcx.guanghe520.com/user/signin",getToken)
            .then((res)=>{
              //声明信息，注册老师
              let regTeacher = this.$qs.stringify({
                nickName : _that.nickNameV,
                name : _that.nameV,
                password : _that.passwordV,
                avatar : _that.headImgV
              });
              //注册老师
              axios.post("https://xcx.guanghe520.com/user/registerTeacher",regTeacher,{
                headers:{
                  Authorization:res.data.token
                }
                //成功回到首页
              }).then((res)=>{
                console.log(res);
                if(res.data.message === "成功创建新用户!"){
                  alert("注册成功！！！");
                  this.$router.push({ path:'/'})
                }else{
                  alert(res.data.message)
                }
              }).catch((err)=>{
                alert('提交失败!!!')
              })
            })
        }
      }
    }
</script>

<style scoped>
    .body{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    .body .bodyhead{
      width: 100%;
      height: 70px;
      background-color: #fff;
      font-size: 20px;
      color: black;
      font-weight: 600;
      line-height: 70px;
      padding-left: 20px;
    }
    .body .bodyCenter{
      width: 98%;
      height: auto;
      background-color: #fff;
      margin: 10px auto;
      padding: 20px;
    }
    .body .bodyCenter h2{
      font-size: 18px;
      font-weight: 600;
      color: #000;
      border-left: 4px solid #FF890E;
      padding-left: 8px;
    }
    .body .bodyCenter .megBox{
      width: 90%;
      height: auto;
      margin: 10px;
    }
    .body .bodyCenter .nickName{
      width: 100%;
      height: 100px;
      position: relative;
      line-height: 100px;
      display: flex;
      align-items: center;
    }
    .body .bodyCenter .nickName p{
      font-size: 14px;
      color: #000;
      font-weight: 600;
    }
    .body .bodyCenter .nickName p span{
      color: red;
    }
    .body .bodyCenter .nickName input{
      width: 500px;
      height: 40px;
      border: 1px solid #aaa;
      position: absolute;
      left: 200px;
      font-size:16px;
      padding-left: 10px;
    }
    .body .bodyCenter .nickName input::-webkit-input-placeholder{
      text-align: center;
    }
    .body .bodyCenter .headImg{
      width: 100%;
      height: 200px;
      position: relative;
      display: flex;
      align-items: start;
      margin-top: 50px;
    }
    .body .bodyCenter .headImg p{
      font-size: 14px;
      color: #000;
      font-weight: 600;
      padding-right: 170px;
    }
    .body .bodyCenter .regBtn{
      width: 100px;
      height: 35px;
      background-color: #FF890E;
      color: #fff;
      font-size: 14px;
      line-height: 35px;
      text-align: center;
      margin: auto;
      margin-top: 100px;
      font-weight: 600;
    }
</style>



