import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index'
import releaseEvery from '../pages/releaseEvery'
import releaseBoss from '../pages/releaseBoss'
import test from '../pages/test'
import regTeacher from '../pages/regTeacher'
import caseStudyRoom from '../pages/caseStudyRoom'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path:"/releaseEvery",
      name:'releaseEvery',
      component:releaseEvery
    },
    {
      path:"/test",
      name:'test',
      component:test
    },
    {
      path:"/releaseBoss",
      name:'releaseBoss',
      component:releaseBoss
    },
    {
      path:"/regTeacher",
      name:'regTeacher',
      component:regTeacher
    },
    {
      path: "/caseStudyRoom",
      name: 'caseStudyRoom',
      component: caseStudyRoom
    }
    ]
})



