<template>
    <div class="body">
      <div class="head">
        发布 > BOSS说
      </div>
      <div class="center">
        <h1>基本信息</h1>
        <div class="centerBox">
          <div class="centerLeft">
            <p class="pZero">选择老师<span>  *</span></p>
            <p class="pOne">主标题<span>  *</span></p>
            <p class="pTwo">上传头像<span>  *</span></p>
            <p class="pOne">视频上传<span>  *</span></p>
            <p class="pTwo">上传封面<span>  *</span></p>
            <p class="pTwo">视频切片</p>
            <p class="pThree">图文详情<span>  *</span></p>
          </div>
          <div class="centerRight">
            <div class="divZero">
              <drop-down
                @getChangeTea = getTea
                :itemlist="itemlist"
              ></drop-down>
            </div>
            <div class="divOne">
              <input type="text" placeholder="请输入主标题（建议在15个字符以内）" v-model="lastTitle"/>
                  0/15
            </div>
            <div class="divThree">
             <boss-img
              class="bossImgC"
             ></boss-img>
              <p>建议尺寸100*100px或1:1，JPG,PNG格式，图片小于5M</p>
            </div>
            <div class="divTwo">
              <p>格式支持mp3、m4a，为保证音频加载与播放得流畅性,建议上传大小不超过500M</p>
              <token></token>
            </div>
            <div class="divThree">
              <token2
                class="token2"
              ></token2>
              <p>建议尺寸300*300px或1:1，JPG,PNG格式，图片小于5M</p>
            </div>
            <div class="divFour">
              <token3></token3>
              <p>建议尺寸300*300px或1:1，JPG,PNG格式，图片小于5M</p>
            </div>
            <div class="divFive">
              <u-e :defaultMsg=defaultMsg :config=config ref="ue"></u-e>
            </div>
          </div>
        </div>
        <h1>上架设置</h1>
        <div class="setUpOp">
          <p class="timeP">
            上架时间<span>   *</span>
          </p>
          <el-radio-group v-model="radio2" class="radioBox" @change="changeValue">
            <el-radio :label="0">立即上架</el-radio>
            <el-radio :label="1">定时上架</el-radio>
            <el-date-picker
              v-model="setUpTime"
              type="datetime"
              placeholder="选择日期时间"
              v-if="showDate"
            >
            </el-date-picker>
            <el-radio :label="2">暂不上架</el-radio>
          </el-radio-group>
        </div>
        <div class="submitBtn"  @click="goSumbit">发布内容</div>
      </div>
    </div>
</template>
<script>
  import token from '../components/token'
  import token2 from '../components/token2'
  import token3 from '../components/token3'
  import BossImg from '../components/BossImg'
  import UE from '../components/UE'
  import dropDown from "../components/dropDown"
  import axios from "axios"
  import Bus from './bus.js'
  import $ from 'jquery'
  let tokens
    export default {
        name: "releaseBossSay",
      data(){
        return{
          itemlist:[],
          defaultMsg: '',
          config: {
            initialFrameWidth: 1000,
            initialFrameHeight: 400
          },
          radio2: 3,
          pickerOptions1: {
            shortcuts: [{
              text: '今天',
              onClick(picker) {
                picker.$emit('pick', new Date());
              }
            },
              {
                text: '昨天',
                onClick(picker) {
                  const date = new Date();
                  date.setTime(date.getTime() - 3600 * 1000 * 24);
                  picker.$emit('pick', date);
                }
              },
              {
                text: '一周前',
                onClick(picker) {
                  const date = new Date();
                  date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', date);
                }
              }
            ]
          },
          setUpTime: '',
          lastAudioUrl:"",
          publicTeaMrg:[],
          teacherName:"",
          showDate:false,
          lastUpTime:"",
          lastContent:"",
          lastTitle:"",
          teacherId:"",
          lastHeadImg:"",
          videoPictures:"",
          msecUp:"",
          TeaHeadImg:""
        }
      },
      components:{
          token,
          token2,
          token3,
          UE,
          dropDown,
          BossImg
      },
      mounted(){
        //通过bus接受token组件的音频(前期是音频，后期是视频)
        Bus.$on('passAudio', (e) => {
          this.lastAudioUrl = e;
        });

      },
      methods:{
          //点击提交按钮
        goSumbit(){
          this.lastHeadImg = $(".token2").children(".picss").children("img").attr("src");
          this.TeaHeadImg = $(".bossImgC").children(".BossImgs").children("img").attr("src");
          //获取富文本编辑器内容
          let content = this.$refs.ue.getUEContent();
          this.$notify({
            title: '获取成功，可在控制台查看！',
            message: content,
            type: 'success'
          });
          this.lastContent = content;
          //通过选中的时间来进行决定什么时候上传
          //立即上架
          if(this.lastUpTime === 0){
            this.setUpTime = new Date();
            let _that = this;
            //用户登录请求token
                  let allData = this.$qs.stringify({
                    authorId:_that.teacherId,
                    nickName:_that.teacherName,
                    avatar:_that.TeaHeadImg,
                    title:_that.lastTitle,
                    content:_that.lastContent,
                    videoURL:_that.lastAudioUrl,
                    cover:_that.lastHeadImg,
                    videoPictures:_that.videoPictures,
                    publishTime:new Date(),
                    courseType:"2"
                  });
                  axios.post("https://xcx.guanghe520.com/courseware/create",allData,{
                    headers:{
                      Authorization:tokens
                    }
                  }).then((res)=>{
                    alert(res.data.message);
                    this.$router.push({path:"/"})
                  }).catch((err)=>{
                    alert(err)
                  })
              .catch((err)=>{
                console.log(err)
              })
          }
          if(this.lastUpTime === 1){
              let _that = this;
              //用户登录请求token
                  let allData = this.$qs.stringify({
                    authorId:_that.teacherId,
                    nickName:_that.teacherName,
                    title:_that.lastTitle,
                    content:_that.lastContent,
                    videoURL:_that.lastAudioUrl,
                    cover:_that.lastHeadImg,
                    videoPictures:_that.videoPictures,
                    publishTime:_that.setUpTime,
                    courseType:"2"
                  });
                  axios.post("https://xcx.guanghe520.com/courseware/create",allData,{
                    headers:{
                      Authorization:tokens
                    }
                  }).then((res)=>{
                    alert(res.data.message)
                  }).catch((err)=>{
                    alert(err)
                  })
                .catch((err)=>{
                  console.log(err)
                })
          }
          if(this.lastUpTime === 2){
            alert("暂不上架成功");
            this.$router.push({path:"/"})
          }
        },
        changeValue(value){
          //根据单选框的点击判断，假如选中了定时上传，才显示时间选择器
          if(this.radio2 === 1){
            this.showDate = true
          }else{
            this.showDate = false
          }
          //判断点击的是哪个，把他传给最终上传内容的按钮
          if(value === 0){
            this.lastUpTime = 0
          }
          if(value === 1){
            this.lastUpTime = 1
          }
          if(value === 2){
            this.lastUpTime = 2
          }
        },
          //通过子组件事件把选中的老师所有信息拿过来，进行渲染；
          getTea(meg){
            this.publicTeaMrg = meg
            this.teacherName = this.publicTeaMrg.nickName
            // this.beForeImg = this.publicTeaMrg.avatar
            this.teacherId = this.publicTeaMrg._id
          },
      },
      created(){
        //登陆用户获取token
        let getToken = this.$qs.stringify({
          name:"gh_sunwh",
          password:"111111"
        });
        let _that = this
        axios.post("https://xcx.guanghe520.com/user/signin",getToken)
          .then((res)=>{
            tokens = res.data.token
            //获取所有老师的信息
            axios.get("https://xcx.guanghe520.com/user/teacherList",{
              headers:{
                Authorization:tokens
              }
            })
              .then((res)=>{
                _that.itemlist = res.data.teachers
              })
              .catch((err)=>{
                console.log(err)
              })
          })
          .catch((err)=>{
            console.log(err)
          })
      }
    }
</script>
<style scoped>
  .body{
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .body .head{
    width: 100%;
    height: 70px;
    background-color: white;
    font-size: 14px;
    color: #555;
    font-weight: 600;
    line-height: 70px;
    padding-left: 30px;
  }
  .body .center{
    width: 98%;
    background-color: white;
    margin: 10px auto;
    padding: 30px;
  }
  .body .center h1{
    font-size: 18px;
    color: #333;
    border-left: 3px solid #FF890E;
    font-weight: 600;
    padding-left: 10px;
  }
  .body .center .centerBox{
    width: 100%;
    height: 100%;
    display: flex;
    margin-top: 30px;
  }
  .body .center .centerBox .centerLeft{
    width: 15%;
    height: 100%;
    padding-left: 20px;
  }
  .body .center .centerBox .centerLeft span{
    color: #f00;
  }
  .body .center .centerBox .centerLeft .pZero{
    height: 280px;
    font-size: 16px;
    color: #000;
    font-weight: 600;
  }
  .body .center .centerBox .centerLeft .pOne{
    height: 120px;
    font-size: 16px;
    color: #000;
    font-weight: 600;
  }
  .body .center .centerBox .centerLeft .pTwo{
    height: 240px;
    font-size: 16px;
    color: #000;
    font-weight: 600;
  }
  .body .center .centerBox .centerLeft .pThree{
    height: 550px;
    font-size: 16px;
    color: #000;
    font-weight: 600;
  }
  .body .center .centerBox .centerRight{
    width: 85%;
    height: 100%;
  }
  .body .center .centerBox .centerRight  .divZero{
    height: 280px;
  }
  .body .center .centerBox .centerRight .divOne{
    height: 120px;
    color: #666;
  }
  .body .center .centerBox .centerRight .divTwo{
    height: 120px;
    color: #666;
  }
  .body .center .centerBox .centerRight .divTwo p{
    font-size: 14px;
    padding-bottom: 10px;
  }
  .body .center .centerBox .centerRight .divOne input{
    width: 600px;
    height: 40px;
    border: 1px solid #aaa;
    color: #333;
    font-size: 14px;
    line-height: 40px;
    padding-left: 10px;
    margin-right: 10px;
  }
  .body .center .centerBox .centerRight .divOne input::-webkit-input-placeholder{
    text-align: center;
  }
  .body .center .centerBox .centerRight .divThree{
    height: 240px;
    color: #666;
    display: flex;
    align-items: start;
  }
  .body .center .centerBox .centerRight .divFour{
    height: 240px;
    color: #666;
    display: flex;
    align-items: start;
  }
  .body .center .centerBox .centerRight .divFive{
    height: 550px;
    color: #666;
    display: flex;
    align-items: start;
  }
  .body .center .setUpOp{
    width: 100%;
    height: 200px;
    padding: 30px;
    display: flex;
  }
  .body .center .setUpOp p{
    font-size: 16px;
    color: #000;
    font-weight: 600;
  }
  .body .center .setUpOp span{
    color: #f00;
  }
  .body .center  .radioBox{
    display: flex;
    flex-direction: column;
    margin-left: 70px;
    position: relative;
  }
  .body .center  .radioBox .el-radio{
    margin-bottom: 20px;
  }
  .body .center  .radioBox .el-date-editor{
    position: absolute;
    left: 110px;
    top: 20px;
  }
  .body .center  .submitBtn{
    width: 100px;
    height: 35px;
    background-color: #FF890E;
    color: #fff;
    font-size: 14px;
    line-height: 35px;
    text-align: center;
    margin: auto;
  }
</style>



