<template>
  <div class="body">
    <div class="bodyLeft">
      <div class="bodyLeftLogo">
        <img src="/static/img/logo.png" alt="">
      </div>
      <div class="bodyLeftContent">
        <div class="zeroMenu" @click="toReg">
          <h2>注册老师</h2>
          <i class="fa fa-chevron-right" aria-hidden="true" ></i>
        </div>

        <div class="firstMenu" @click="secondMenuShow">
          <h2>内容管理</h2>
          <i class="fa fa-chevron-right" aria-hidden="true" v-show="!iconShow"></i>
          <i class="fa fa-chevron-down" aria-hidden="true" v-show="iconShow"></i>
        </div>
        <div class="secondMenu" v-show="iconShow">
          <p @click="everyDay">每日主持词</p>
          <p @click="everyDay">BOSS说</p>
          <p @click="everyDay">案例研习室</p>
        </div>
      </div>
    </div>
    <div class="bodyRight">
      <release-boss-say></release-boss-say>
    </div>
  </div>
</template>

<script>
  import releaseBossSay from '../components/releaseBossSay'
  export default {
    name: "releaseBoss",
    data(){
      return{
        iconShow:true,
      }
    },
    components:{
      releaseBossSay
    },
    methods:{
      everyDay(){
        this.$router.go(-1);
      },
      secondMenuShow(){
        this.iconShow = !this.iconShow
      },
      toReg(){
        this.$router.push({ path:'/regTeacher'})
      }

    }
  }
</script>

<style scoped>
  .body{
    width: 100%;
    height: 100%;
    display: flex;
    background-color: #F6F6F6;
  }
  .body .bodyLeft{
    width: 15%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
  }
  .body .bodyLeft .bodyLeftLogo{
    width: 100%;
    height: 74px;
    background-color:#FF890E ;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .body .bodyLeft .bodyLeftLogo img{
    width: 70%;
    height: 60%;
  }
  .body .bodyLeft .bodyLeftContent{
    width: 100%;
    height:100%;
  }
  .body .bodyLeft .bodyLeftContent .firstMenu {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .body .bodyLeft .bodyLeftContent .firstMenu:hover{
    cursor: pointer;
  }
  .body .bodyLeft .bodyLeftContent .firstMenu h2{
    font-size: 24px;
    color: #000;
    font-weight: 400;
    line-height: 70px;
    padding-left: 16px;
  }
  .body .bodyLeft .bodyLeftContent .firstMenu i{
    padding-right: 20px;
  }
  .body .bodyLeft .bodyLeftContent .secondMenu{
    width: 100%;
    height: 50px;
  }
  .body .bodyLeft .bodyLeftContent .secondMenu p{
    font-size: 14px;
    color: #999;
    text-align: left;
    padding-left: 16px;
    line-height: 50px;
    font-weight: 600;
  }
  .body .bodyRight{
    width: 85%;
    height: 100%;
  }
  .body .bodyLeft .bodyLeftContent .zeroMenu {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .body .bodyLeft .bodyLeftContent .zeroMenu:hover{
    cursor: pointer;
  }
  .body .bodyLeft .bodyLeftContent .zeroMenu h2{
    font-size: 24px;
    color: #000;
    font-weight: 400;
    line-height: 70px;
    padding-left: 16px;
  }
  .body .bodyLeft .bodyLeftContent .zeroMenu i{
    padding-right: 20px;
  }

</style>



