<template>
    <div class="token">
      <form action="upload" method="post" enctype="multipart/form-data">
        <div class="modify">
          选择文件
          <input class="file-input" type="file" id="select" />
        </div>
      </form>
      <div id="result">
      </div>
      <p class="pics"></p>
    </div>
</template>
<script  type="text/javascript">
  var result;
  var fileName;
  var bigNum;
  var marker = '';
  var domain = 'http://poaadnrl9.bkt.clouddn.com/';
  import $ from 'jquery'
  import Bus from './bus.js'
  export default {
        name: "token",
        data(){
           return{
           }
        },
        mounted() {
          var complete = function (res) {
            $('#result').html('<img src=' + result.domain + '/' + fileName + ' />');
          };
          var subObject = {
            next(res) {
            },
            error(err) {
              console.log(err);
            },
            complete(res) {
              let _that = this;
              bigNum = res.key;
              $.ajax({
                type: "get",
                url: "https://xcx.guanghe520.com/qiniu/getImg",
                async: true,
                data: {
                  marker: marker
                },
                success: function (res){
                  var html = "";
                  html += bigNum + ' √';
                  $('.pics').html(html);
                  Bus.$emit('passAudio',domain+bigNum);
                }
              });
            }
          };
          $.ajax({
            type: "GET",
            url: "https://xcx.guanghe520.com/qiniu/uptoken",
            async: true,
            success: function (jsonpCallback) {
              result = jsonpCallback;
            }
          });

          $('#select').on('change', function () {
            // 文件资源名为 null 或者 undefined，则文件资源名会以 hash 值作为资源名
            // 避免文件重名导致无法上传成功
            var file = this.files[0];
            fileName = 'image/test/' + (new Date()).valueOf() + '_' + file.name;
            var token = result.uptoken;
            var domain = result.domain;
            var config = {
              useCdnDomain: true,
              disableStatisticsReport: false,
              retryCount: 6,
              region: qiniu.region.z0
            };
            var putExtra = {
              fname: "",
              params: {},
              mimeType: null
            };
            var observable = qiniu.upload(file, fileName, token, putExtra, config);
            // 上传开始

            var subscription = observable.subscribe(subObject);
            // 上传取消
            // subscription.unsubscribe()
          });
        }
  }
</script>

<style scoped>
  .token{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .modify{
    width: 100px;
    height: 40px;
    border: 1px solid #aaa;
    position: relative;
    font-size: 14px;
    text-align: center;
    line-height: 40px;
    color: #000;
  }
  .file-input{
    width: 100px;
    height: 40px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
  }
  .pics{

  }
  .pics img{

  }
</style>



