<template>
  <div>
    <input type="text" v-model="inputVal"></input>
    <div>{{inputVal}}</div>
  </div>
</template>
<script>
  import dropDown from "../components/dropDown"
  import axios from 'axios'
  export default {
    name:"test",
    data() {
      return {
        inputVal: '',
        timeout: null,
        itemlist:[],
      }
    },
    components:{
      dropDown
    },
    created(){
      let getToken = this.$qs.stringify({
        name:"gh_daom",
        password:"111111"
      });
      let _that = this;
      axios.post("https://xcx.guanghe520.com/user/signin",getToken)
        .then((res)=>{
          let allData = this.$qs.stringify({
            authorId:"1111",
            nickName:"1111",
            avatar :"1111",
            title :"1111",
            content  :"111",
            videoURL :"1111",
            cover :"11111",
            publishTime :"1111",
          });
          axios.get("https://xcx.guanghe520.com/courseware/list?pageSize=2&pageNumber=1&courseType=3",{
            headers:{
              Authorization:res.data.token
            }
          }).then((res)=>{
            console.log(res)
          }).catch((err)=>{
            console.log(err)
          })
        })
        .catch((err)=>{
          console.log(err)
        })
    }
  }
</script>

